
.control-panel {
  position: fixed;
  left: 100px;
  top: 60px;
  width: 280px;
  height: 30px;
  background-color: #30465f;
  z-index: 1000;
  overflow: hidden;
  color: #e4e4e4;
  font-size: 12px;
  border-radius: 5px;
  -webkit-transition: height 0.35s cubic-bezier(0.46, 0.83, 0.63, 1), left 0.1s cubic-bezier(0.46, 0.83, 0.63, 1), top 0.1s cubic-bezier(0.46, 0.83, 0.63, 1);
  -moz-transition: height 0.35s cubic-bezier(0.46, 0.83, 0.63, 1), left 0.1s cubic-bezier(0.46, 0.83, 0.63, 1), top 0.1s cubic-bezier(0.46, 0.83, 0.63, 1);
  -ms-transition: height 0.35s cubic-bezier(0.46, 0.83, 0.63, 1), left 0.1s cubic-bezier(0.46, 0.83, 0.63, 1), top 0.1s cubic-bezier(0.46, 0.83, 0.63, 1);
  transition: height 0.35s cubic-bezier(0.46, 0.83, 0.63, 1), left 0.1s cubic-bezier(0.46, 0.83, 0.63, 1), top 0.1s cubic-bezier(0.46, 0.83, 0.63, 1);
}
.control-panel.active {
    height: 600px;
}
.control-panel .control-panel--header {
    width: 100%;
    height: 30px;
    background-color: #1F2D3D;
}
.control-panel .control-panel--header h4 {
      margin: 0 10px;
      font-size: 14px;
      line-height: 30px;
      vertical-align: middle;
      color: #e4e4e4;
      font-weight: 400;
      cursor: move;
}
.control-panel .control-panel--header .control-panel_close {
      display: inline-block;
      float: right;
      margin-right: -10px;
      width: 30px;
      height: 30px;
      font-size: 12px;
      text-align: center;
      cursor: pointer;
}
.control-panel .control-panel--header .control-panel_cntr {
      display: inline-block;
      float: right;
      margin-right: -10px;
      width: 30px;
      height: 30px;
      font-size: 12px;
      text-align: center;
      cursor: pointer;
}
.control-panel .control-panel--cnt {
    width: 100%;
    height: 570px;
    overflow: auto;
}
.control-panel--cnt .el-tabs__header {
  background-color: #324057;
}
.el-tabs__nav {
  width: 100%;
}
.control-panel--cnt .el-tabs__item {
  font-size: 12px;
  height: 32px;
  line-height: 32px;
  color: #e4e4e4;
  width: 50%;
  text-align: center;
}
.control-panel--cnt .el-tabs__item:hover {
    color: #97a8be;
}
.editor-pane {
  padding: 5px 10px;
  color: #475669;
}
.tab-line {
  position: relative;
  height: 1px;
  background-color: #bfcbd9;
  margin: 15px 0;
}
.tab-line .tab-line--title {
    position: absolute;
    height: 24px;
    padding: 0 10px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, 0);
    margin-top: -12px;
    line-height: 24px;
    vertical-align: middle;
    background-color: #D3DCE6;
}
.el-form-item {
  margin-bottom: 10px;
}
.el-form-item__label {
  font-size: 12px;
  padding: 6px 12px 6px 0;
  line-height: 1.5;
  text-align: left;
}
.el-form-item__content {
  line-height: 30px;
}
.br2 input {
  border-radius: 2px;
}
.tag-item {
  display: inline-block;
  height: 100%;
  padding: 0 8px;
  cursor: pointer;
  background-color: transparent;
  border-left: 1px solid transparent;
  border-right: 1px solid transparent;
  -webkit-transition: all .25s;
  -moz-transition: all .25s;
  transition: all .25s;
}
.tag-item.active {
    background-color: #8492A6;
    border-left: 1px solid #E5E9F2;
    border-right: 1px solid #E5E9F2;
}



