
.tree-text .tree-text__cntrl {
  display: inline-block;
  width: 20px;
  text-align: center;
  cursor: pointer;
}
.tree-text:hover {
  background-color: #1f2d3d;
  cursor: pointer;
}
.tree-text:hover .tree-text__cntrl {
    color: #20a0ff;
}
.tree-text.active {
  background-color: #1f2d3d;
}
.tree-text.active .tree-text__cntrl {
    color: #20a0ff;
}
.collapse-enter, .collapse-leave {
  opacity: 0;
  transform: translate(-5%, 0);
}
.collapse-enter-active, .collapse-leave-active {
  transition: all .3s;
  transform-origin: center top;
}
.pl20 {
  padding-left: 20px;
}



