
.op-line.op-line_t .op-circle, .op-line.op-line_r .op-circle, .op-line.op-line_b .op-circle, .op-line.op-line_l .op-circle {
  width: 12px;
  height: 12px;
  position: absolute;
  background-color: #eef1f6;
  border: 1px solid #20a0ff;
  border-radius: 6px;
  box-sizing: border-box;
  cursor: crosshair;
  z-index: 1;
}
.operate-box {
  display: none;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 80;
}
.operate-box.active {
    display: block;
}
.op-line {
  position: absolute;
}
.op-line.op-line_t {
    width: 100%;
    top: -2px;
    border-top: 2px dashed #20a0ff;
}
.op-line.op-line_t .op-circle {
      top: -7px;
      left: 50%;
      margin-left: -6px;
}
.op-line.op-line_r {
    height: 100%;
    right: -2px;
    border-left: 2px dashed #20a0ff;
}
.op-line.op-line_r .op-circle {
      right: -5px;
      top: 50%;
      margin-top: -6px;
}
.op-line.op-line_b {
    width: 100%;
    bottom: -2px;
    border-top: 2px dashed #20a0ff;
}
.op-line.op-line_b .op-circle {
      bottom: -5px;
      left: 50%;
      margin-left: -6px;
}
.op-line.op-line_l {
    height: 100%;
    left: -2px;
    border-left: 2px dashed #20a0ff;
}
.op-line.op-line_l .op-circle {
      left: -7px;
      top: 50%;
      margin-top: -6px;
}



