
.ctrl-scale {
  position: fixed;
  right: 300px;
  bottom: 50px;
  width: 130px;
  height: 36px;
  word-spacing: -6px;
  z-index: 1000;
}
.ctrl-scale .ctrl-scale__minus {
    display: inline-block;
    position: relative;
    cursor: pointer;
    height: 100%;
    width: 40px;
    vertical-align: middle;
    background-color: #30465f;
    border-radius: 18px 0 0 18px;
    word-spacing: 0;
}
.ctrl-scale .ctrl-scale__minus .minus {
      width: 10px;
      height: 2px;
      position: absolute;
      top: 50%;
      left: 50%;
      -webkit-transform: translate(-50%, -50%);
      -moz-transform: translate(-50%, -50%);
      transform: translate(-50%, -50%);
      background-color: #fff;
}
.ctrl-scale .ctrl-scale__minus.disabled .minus {
      background-color: #808080;
}
.ctrl-scale .ctrl-scale__minus:hover {
      background-color: #1f2d3d;
}
.ctrl-scale .ctrl-scale__plus {
    display: inline-block;
    position: relative;
    cursor: pointer;
    height: 100%;
    width: 40px;
    vertical-align: middle;
    background-color: #30465f;
    border-radius: 0 18px 18px 0;
    word-spacing: 0;
}
.ctrl-scale .ctrl-scale__plus .plus-h {
      width: 10px;
      height: 2px;
      position: absolute;
      top: 50%;
      left: 50%;
      -webkit-transform: translate(-50%, -50%);
      -moz-transform: translate(-50%, -50%);
      transform: translate(-50%, -50%);
      background-color: #fff;
}
.ctrl-scale .ctrl-scale__plus .plus-v {
      width: 2px;
      height: 10px;
      position: absolute;
      top: 50%;
      left: 50%;
      -webkit-transform: translate(-50%, -50%);
      -moz-transform: translate(-50%, -50%);
      transform: translate(-50%, -50%);
      background-color: #fff;
}
.ctrl-scale .ctrl-scale__plus.disabled .plus-h, .ctrl-scale .ctrl-scale__plus.disabled .plus-v {
      background-color: #808080;
}
.ctrl-scale .ctrl-scale__plus:hover {
      background-color: #1f2d3d;
}
.ctrl-scale .ctrl-scale__text {
    display: inline-block;
    position: relative;
    cursor: pointer;
    height: 100%;
    width: 50px;
    vertical-align: middle;
    background-color: #30465f;
    word-spacing: 0;
    color: #fff;
    font-size: 14px;
    text-align: center;
    line-height: 36px;
}
.ctrl-scale__list {
  position: absolute;
  width: 100%;
  bottom: 50px;
  color: #fff;
  background-color: #30465f;
}
.ctrl-scale__list ul {
    margin: 0;
    padding: 0;
    list-style: none;
}
.ctrl-scale__list ul li {
      padding: 5px 15px;
      cursor: pointer;
}
.ctrl-scale__list ul li:hover {
        background-color: #28394e;
}
.ctrl-scale__list ul li.active {
        background-color: #1f2d3d;
}
.ctrl-scale__list .trangle {
    position: absolute;
    left: 50%;
    margin-left: -10px;
    border-top: 10px solid #30465f;
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
}
.s-label {
  float: right;
  color: #bbb;
}



