
.tree-box {
  background-color: #28394e;
  color: #bfcbd9;
  margin: 10px auto;
  font-size: 12px;
}
.tree-box__ul {
  list-style: none;
  padding-left: 0;
}
.tree-box__ul.dragging {
    margin: 5px 0;
    border-top: 1px solid #fff;
    border-bottom: 1px solid #fff;
}
.tree-box__ul li {
    line-height: 1.5;
    transition: all .3s;
}



